import React, {Component} from 'react';

class Next extends Component {
	constructor(props) {
		super(props)
		this.changePage = this.changePage.bind(this);
	}
	changePage(page) {
		if (this.props.completed >= this.props.toNum)
			this.props.changePage(this.props.page+1)
	}
	render() {

		if (this.props.completed === this.props.questions) {
			return (
				<div className={"btn btn-cta"} onClick={()=>this.props.to('/results')} >
					Check Results
				</div>
			);
		} else {

		return (
			<div className={(this.props.completed>=this.props.toNum ? "btn-cta" : "disabled") + " btn" } onClick={this.changePage}>
				Next Page
			</div>
			);
		}	
	}
}

export default Next;


