import React, {Component} from 'react';
import './style.css';

class Admin extends Component {
	render() {
		return (
			<div className="container sca-intro sca-admin">
				<h2>
					Welcome Admin
				</h2>
				<p>
					Please select from the tools below:
				</p>
				<ul className="list-unstyled">
					<li><div className="btn btn-cta" onClick={() => this.props.to('/results') }>Student Results</div></li>
					
					<li><div className="btn" onClick={() => this.props.to('/results/business') }>Business Results</div></li>
				</ul>
				
				
			</div>		
		);
	}
}

export default Admin;


