import React, {Component} from 'react';

/*eslint-disable*/

const Label = ({ defaultValue, changed, value, handleChange }) => (
	<label className={(defaultValue==value && changed===true) ? 'active' : null} >
		{value}
		<input type="radio" name="options" autoComplete="off" value={value} onClick={handleChange} />
	</label>
)

class Question extends Component {
	constructor(props) {
		super(props)
		this.state = {
				value: this.props.defaultValue
			}
		this.handleChange = this.handleChange.bind(this);
	}
	handleChange(e) {
		let answer = {
			id: this.props.index,
			value: e.target.value,
			rawscore: this.props.rawscore,
			type: this.props.type,
			subtype: this.props.subtype,
		}
		this.props.answerQuestion(answer);
	}
	render() {

		// Check if answers have been all filled out
		let text = <span>{this.props.title}</span>;

		if (this.props.changed === true) {
			text = <b className="text-success">{this.props.title}</b>
		}
		if (this.props.checkResults === true && this.props.changed !== true) {
			text = <b className="text-danger">{this.props.title}</b>
		}
		
		return (
			<div className="question" >
				<div className="holder">
					<h3>{this.props.index+1}</h3>
					{text}
					<div className="row ranger">
						<span className="pull-left">Not Like Me</span>	
						<span className="pull-right">Like Me</span>	
						<Label defaultValue={this.props.defaultValue} changed={this.props.changed} value={1}  handleChange={this.handleChange}  />
						<Label defaultValue={this.props.defaultValue} changed={this.props.changed} value={2}  handleChange={this.handleChange}  />
						<Label defaultValue={this.props.defaultValue} changed={this.props.changed} value={3}  handleChange={this.handleChange}  />
						<Label defaultValue={this.props.defaultValue} changed={this.props.changed} value={4}  handleChange={this.handleChange}  />
						<Label defaultValue={this.props.defaultValue} changed={this.props.changed} value={5}  handleChange={this.handleChange}  />	
					</div>
				</div>
				<div className="image" style={{ backgroundImage: 'url(' + this.props.image + ')' }}></div>
			</div>
		);
	}
}



export default Question;


