import Moment from 'moment';
import _ from 'underscore';
import axios from 'axios';
/*eslint-disable*/

let api = axios.create({ baseURL: 'http://per01.vpnaus.com/api/' });

// Self Made React Router
export function to(url) {
	this.setState({url: url});
	this.calcResults();
}

// Caculate time spend on test
export function time(time) {
	const formatedDate = Moment(this.state.begin).toNow(true);;
	this.setState({time: formatedDate})
}

export function tick(time) {
	const formatedDate = Moment(this.state.begin).toNow(true);;
	this.setState({time: formatedDate})
}

export function handleName(name) {
	name = name.target.value;
	this.setState({ name: name })
}

export function handleEmail(email) {
	email = email.target.value;
	this.setState({ email: email })
}

export function sendResults() {
	if (this.state.finished) {
		if (!this.state.apiSent) {
			console.log('sending results')
			let profile = {
				name: this.state.name,
				email: this.state.email,
				score: this.state.score,
				total: this.state.total,
				begin: this.state.begin,
				percent: this.state.percent,
				criterias: this.state.criterias,
				questions: this.state.questions
			}
			api.post('/', profile)
				.then(res => console.log(res));
			this.state.apiSent = true;
		}
	} else {
		console.log('not complete yet')
	}
	
}


// Update Each time question answered
export function answerQuestion(answer) {

	// Update Question DefaultValue and Value
	let questions = this.state.questions
	questions[answer.id].defaultValue = answer.value;
	questions[answer.id].changed = true;
	this.setState({questions: questions});

	// Prepare answers
	let answers = this.state.answers;
	answers[answer.id] = answers;
	this.setState({answers: answers});

	// Set Completed length
	let completed = Object
		.keys(this.state.answers)
		.length;
	this.setState({completed: completed})

	// If copmleted set result as finished
	if (completed === questions.length) {
		this.setState({ finished: true });
		
	}
		
}
// Scroll to top after completing a question
export function handleChangePage(page) {
	this.setState({page: page});
	// window.scrollTo(0, 0);
	smoothScroll("root")
}

// Legacy for after results submitted
export function handleChangeResult(profileIndex) {
	
	this.setState({profile: this.state.profiles[profileIndex]})
}

// Caculate the results
export function calcResults(){
	let criterias = this.state.criteria
	let answers = this.state.questions;
	let totalscore = 0;
	let totaltotal = 0;
	let percent = 0;

	criterias.map((criteria, cindex) => {
		
		// Calc Overall Score 
		// Collect all results of top level criteria
		let results = _.where(answers, { type: criteria.type });
		let score = 0;
		results.map((result, index) => {

			// Reverse score for inverse values;
			let val = result.defaultValue * result.inverse;
			if (val < 0) { val = val + 6 }
			// remove 1 as questions can only be 0-4
			score = score + val -1;
		});

		// Calc score for criteria
		criterias[cindex].score = score;

		// Calc total score for test (where each question is worth 5 points)
		// Remove 1 length as questions can only score 0-4
		criterias[cindex].total = (results.length * 5) - results.length;
		totaltotal = totaltotal + criterias[cindex].total

		// Calc overall total score 
		totalscore = totalscore + score;

		// Calc percentage based of the score divided by total score
		criterias[cindex].percent = criterias[cindex].score / criterias[cindex].total * 100;

		// Calc Subtype Scores	
		// Map overvall type sub criteria
		criteria.criteria.map((subtype, sindex) => {

			// Reset the base score			
			criterias[cindex].criteria[sindex].score = 0;

			// Get all the questions, and where they are of the same criteria and facet, prepare them for eval
			let results = _.where(answers, { type: criteria.type, subtype: subtype.type });
			results.map((result, index) => {
				let val = result.defaultValue * result.inverse;
				if (val < 0) { val = val + 6 }
				criterias[cindex].criteria[sindex].score = criterias[cindex].criteria[sindex].score + val -1;
			})

			// Calc facit and percentage
			criterias[cindex].criteria[sindex].total = ( results.length * 5 ) - results.length ;
			criterias[cindex].criteria[sindex].percent = criterias[cindex].criteria[sindex].score / criterias[cindex].criteria[sindex].total * 100;

		});

		// Order facets by percentages in desc
		criterias[cindex].criteria = _.sortBy(criterias[cindex].criteria, 'percent').reverse();

	});

	percent = totalscore / totaltotal * 100;

	// Sort results by the highest to lowest
	criterias = _.sortBy(criterias, 'percent').reverse();

	// Place Grit Score Last
	let indexOfGrit = _.findIndex(criterias, { title: 'Grit' });
	criterias.push(criterias.splice(indexOfGrit, 1)[0]);

	// Prepare correct profile
	let profileIndex = _.findIndex(this.state.profiles, { profile: criterias[0].criteria[0].profile });

	// If the profile is matched, set it to show as default	
	if (!(profileIndex == -1)) {
		this.setState({ profile : this.state.profiles[profileIndex] })
	}

	this.setState({ criterias: criterias })
	this.setState({ score: totalscore })
	this.setState({ total: totaltotal })
	this.setState({ percent: percent })
}




// Scroll to the top functions
function currentYPosition() {
    // Firefox, Chrome, Opera, Safari
    if (self.pageYOffset) return self.pageYOffset;
    // Internet Explorer 6 - standards mode
    if (document.documentElement && document.documentElement.scrollTop)
        return document.documentElement.scrollTop;
    // Internet Explorer 6, 7 and 8
    if (document.body.scrollTop) return document.body.scrollTop;
    return 0;
}

function elmYPosition(eID) {
    var elm = document.getElementById(eID);
    var y = elm.offsetTop;
    var node = elm;
    while (node.offsetParent && node.offsetParent != document.body) {
        node = node.offsetParent;
        y += node.offsetTop;
    } return y;
}

function smoothScroll(eID) {
    var startY = currentYPosition();
    var stopY = elmYPosition(eID);
    var distance = stopY > startY ? stopY - startY : startY - stopY;
    if (distance < 100) {
        scrollTo(0, stopY); return;
    }
    var speed = Math.round(distance / 100);
    if (speed >= 20) speed = 20;
    var step = Math.round(distance / 25);
    var leapY = stopY > startY ? startY + step : startY - step;
    var timer = 0;
    if (stopY > startY) {
        for ( var i=startY; i<stopY; i+=step ) {
            setTimeout("window.scrollTo(0, "+leapY+")", timer * speed);
            leapY += step; if (leapY > stopY) leapY = stopY; timer++;
        } return;
    }
    for ( var i=startY; i>stopY; i-=step ) {
        setTimeout("window.scrollTo(0, "+leapY+")", timer * speed);
        leapY -= step; if (leapY < stopY) leapY = stopY; timer++;
    }
}


