import React, {Component} from 'react';

class Prev extends Component {
	constructor(props) {
		super(props)
		this.changePage = this.changePage.bind(this);
	}
	changePage(page) {
		this.props.changePage(this.props.page-1)
	}
	render() {
		if(this.props.page===1) return null
		return (
			<div className={" btn prev" } onClick={this.changePage}>
				Back
			</div>
		);
	}
}

export default Prev;


