import React from 'react';
import Question from './Question';

const QuestsionsMap = (props) => {
	const torender = props.questions.map((question, index) => {
			return <Question
				key={index}
				index={index+props.from}
				title={question.title}
				rawscore={question.rawscore}
				changed={question.changed}
				type={question.type}
				subtype={question.subtype}
				image={"images/"+question.type+question.subtype+(index+1)+".jpg"}
				checkResults={props.handleCheckResults}
				answerQuestion={props.answerQuestion}
				defaultValue={question.defaultValue} />
	});
	return(
		<div>
			{torender }
		</div>
		);
}

export default QuestsionsMap;


