import React, {Component} from 'react';
import './style.css';
import Summary from './Summary/Summary';
import MapQuestions from './Question/MapQuestions';
import Next from './Buttons/Next';
import Prev from './Buttons/Prev';






class Questions extends Component {
	constructor(props) {
		super(props)
		this.state = {
			checkResults: false,
			page: this.props.page,
		}
		this.handleCheckResults = this.handleCheckResults.bind(this);
		this.changePage = this.changePage.bind(this)
	}
	componentDidMount(node) {
		this.interval = setInterval(this.updateTime.bind(this), 10000);
	}
	componentWillUnmount() {
		clearInterval(this.interval);
	}
	updateTime() {
		this.props.tick(this.props.time + 1);
	}
	handleCheckResults() {
		this.setState({ checkResults: !this.state.checkResults })
	}
	changePage(page){
		this.setState({ page: page });
		this.props.handleChangePage(page);

	}
	render() {
		let from = (5 * this.state.page)-5;
		let to = (5 * this.state.page);
		let questions = this.props.questions.slice(from, to);
		let style = {
			width: Math.round(this.props.completed/this.props.questions.length*100)+"%",
		}
		return (
			<div className="container">
				<div className="psych">
					<Summary 
						completed={this.props.completed} 
						questions={this.props.questions.length} 
						page={this.state.page}
						time={this.props.time}
						style={style}
						to={this.props.to}
						/>
					<div className="questions">
						<MapQuestions 
							questions={questions} 
							from={from}
							handleCheckResults={this.handleCheckResults}
							answerQuestion={this.props.answerQuestion}
							page={this.state.page}  />
						<Prev 
							page={this.state.page}
							completed={this.props.completed}
							to={to}
							changePage={this.changePage} />
						<Next 
							page={this.state.page}
							completed={this.props.completed}
							questions={this.props.questions.length}
							toNum={to}
							to={this.props.to}
							changePage={this.changePage} />
					</div>
				</div>
			</div>
		);
	}
}

export default Questions;


