import React, {Component} from 'react';
import Circle from '../../Containers/Graphs/Circle';
import './style.css';
import _ from 'underscore';

class Results extends Component {

	componentDidMount() {
		this.props.sendResults()
	}
	

    render() {
		return (
			<div className="resultcard">
				<div className="container ">

					<div
						className="image"
						style={{
						backgroundImage: `url(images/${this.props.profile.picture})`
					}}></div>	

					<h2>
						{this.props.profile.title}
					</h2>
					<div className="quote">
						{this.props.profile.quote}
					</div>

					

					<ul className="list-unstyled list-inline">
						<li>
							<div className="criteria text-center">
								<Circle
									value={this.props.criterias[0].percent}
									progressStroke={this.props.criterias[0].color}/>
								<div className="title">{this.props.criterias[0].title}</div>
							</div>
						</li>
						<li>
							<div className="criteria text-center">
								<Circle
									value={this.props.criterias[1].percent}
									progressStroke={this.props.criterias[1].color}/>
								<div className="title">{this.props.criterias[1].title}</div>
							</div>
						</li>
						<li>
							<div className="criteria text-center">
								<Circle
									value={this.props.criterias[2].percent}
									progressStroke={this.props.criterias[2].color}/>
								<div className="title">{this.props.criterias[2].title}</div>
							</div>
						</li>
					</ul>

					<p>
						{this.props.profile.description}
					</p>

					<div className="attributes">
						<h3>Strengths:</h3>
						<ul>
							{this
								.props
								.profile
								.strengths
								.map((strength, index) => {
									return <li key={index}>{strength}</li>
								})}
						</ul>
					</div>

					<div className="attributes">
						<h3>Areas to watch out for:</h3>
						<ul>
							{this
								.props
								.profile
								.weaknesses
								.map((strength, index) => {
                                    return <li key={index}>{strength}</li>
								})}
						</ul>
					</div>

					<div className="btn btn-print" onClick={()=>window.print()}>Print Results</div>

					
				</div>

			</div>
		);
	}
}

export default Results;


