import React from 'react';
import Check from './Check';

const Summary = (props) => (
	<div id="summary" className="summary" >
		<div className="fake-container">
			<div className="box">
				<h2>Questions Completed</h2>
				<h1 className={props.completed === props.questions ? 'complete' : null}>{props.completed}/{props.questions}</h1>
				<div className="progress-bar-holder">
					<div className="progress-bar" style={props.style}>
						<div className="completed-percent">{props.style.width}</div>
					</div>
					
				</div>
				<h4>Page {props.page}</h4>
				<p>You have spent <b className="text-success">{props.time}</b> doing this test<br /></p>
				<Check to={props.to} completed={props.completed} questions={props.questions} />
			</div>
		</div>
	</div>
)

export default Summary;


