import React, {Component} from 'react';
// import CharBox from './ChartBox.js';
import Circle from '../../Containers/Graphs/Circle';
import './style.css';

class Results extends Component {

	render() {
		return (

			<div className="resultcard">
				<div className="container business ">
					<h2>
						{this.props.name}
					</h2>
					<h3>
						{this.props.profile.title}
					</h3>
					<h4>
						{this.props.profile.quote}
					</h4>

					<p>				
						{this.props.profile.description}
					</p>
					
					<div className="col-sm-12 text-left">
							<div className="attributes">
								<h3>Strengths:</h3>
								<ul>
									{this
										.props
										.profile
										.strengths
										.map((strength, index) => {
											return <li>{strength}</li>
										})}
								</ul>
							</div>

							<div className="attributes">
								<h3>Areas to watch out for:</h3>
								<ul>
									{this
										.props
										.profile
										.weaknesses
										.map((strength, index) => {
											return <li>{strength}</li>
										})}
								</ul>
							</div>
					</div>
					
					

					<div className="row text-center"  >

						<div className="col-sm-12 ">
							<div className=" col-sm-offset-3 col-sm-6">
								<br/>
								<Circle value={this.props.percent} progressStroke="#f6b442" edgeSize={210} radius={100}/>
								<h2>Total</h2>
								<br/>
							</div>
						</div>	

						

						<div className="col-sm-12 ">

							{this.props.criterias.map((criteria, index) => {

								if(index===3) return null

								return (

									<div className="col-sm-4">

										

										<Circle value={criteria.percent} progressStroke={criteria.color} />
										<h3>{criteria.title}</h3>

										<br/>

										

										
										{criteria.criteria.map((facet, i) => {
											return ( 
												<div><Circle
													value={facet.percent}
													progressStroke={criteria.color}
													edgeSize={60}
													radius={25}
													circleStrokeWidth={4} />
													<h4>{facet.title}</h4></div>
											)
										})}	

										{(criteria.criteria.length === 3) ? <div style={{ height: '125px'}}></div> : null }
										

									</div>

								)
								
							})}	
							

						</div>

						

						<div className="col-sm-12 ">
							<br/>
							<div className="col-sm-2">
								<Circle value={this.props.criterias[3].percent} progressStroke="#394f59"/>
							</div>
							<div className="col-sm-10 text-left">
								<h3>Grit Score</h3>
								<p>
									Grit in psychology is a positive, non-cognitive trait based on an individual's
									passion for a particular long-term goal or endstate, coupled with a powerful
									motivation to achieve their respective objective.
								</p>
							</div>
						</div>
						

						

					</div>

				</div>
			</div>
		);
	}
}

export default Results;

