import React, {Component} from 'react';

import './style.css';
import img from './student-study.png'

class Home extends Component {

    render() {

        let ButtonBegin = (
            <a
                href="#"
                className="btn-home btn "
                onClick={() => this.props.to('/questions')}>Begin Now</a>
        )
        if (this.props.finished === true) {
            ButtonBegin = <a
                href="#"
                className="btn-home btn btn-primary "
                onClick={() => this.props.to('/results')}>Check Results</a>
        }
        if (!(this.props.name && this.props.email)) {
            ButtonBegin = <a href="#" className="btn-home btn disabled">Begin Now</a>
        }
        //
        return (
            <div className="sca-welcome">
                <div className="container sca-intro">
                    <h2>
                        Welcome to the Workstyle Indicator
                    </h2>
                    <p>
                        Which one of the below
                        <b>best</b>
                        describes
                        <b className="text-success">you</b>
                        in a work/professional context?
                        <br/>
                        Please answer using a scale of 1 - 5 with 1 being not like you at all and 5
                        being exactly like you.
                    </p>

                    <div className="row text-center">

                        <div
                            className="col-sm-4 col-sm-offset-4"
                            style={{
                            marginBottom: 15
                        }}>
                            <div className="text-left">
                                <b>Your Name:</b>
                            </div>
                            <input
                                type="text"
                                placeholder="Andrew Smith..."
                                className="input-username"
                                onChange={this.props.handleName}
                                value={this.props.name}/>
                        </div>
                        <div
                            className="col-sm-4 col-sm-offset-4"
                            style={{
                            marginBottom: 15
                        }}>
                            <div className="text-left">
                                <b>Your Email:</b>
                            </div>
                            <input
                                type="text"
                                placeholder="email@address.com.au"
                                className="input-password"
                                onChange={this.props.handleEmail}
                                value={this.props.email}/>
                        </div>
                    </div>

                    {ButtonBegin}

                    <div className="row text-center">
                        <div className="col-sm-4 text-right vcenter">
                            <h3>Report Card</h3>
                            <p>See your results based on the answers you provide</p>

                        </div>
                        <div className="col-sm-4 text-center">
                            <div className="img-weclome">
                                <img src={img} alt="Student Studying" height="100%"/>
                            </div>
                        </div>
                        <div className="col-sm-4 text-left vcenter">
                            <h3>Recommendations</h3>
                            <p>Learn what sort of jobs are best suited to you</p>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}

// <div className="btn btn-cta label results-test text-muted" onClick={() =>
// this.props.to('/results') }>Test Results</div>

export default Home;


