import React, {Component} from 'react';

class Check extends Component {
	render() {
		if (this.props.completed !== this.props.questions) return null
		return (
			<div className="btn question btn-primary" onClick={() => this.props.to('/results')}>Check Results</div>
		);
	}
}

export default Check;


