import React, {Component} from 'react';
import './style.css';
import icons from './icons.svg';

// style={{ backgroundImage: `url(${icons})` }}

class SubHeader extends Component {
	render() {

		

		return (
			<div className="sca-subheader">
				<div className="container">
					<ul className="list-inline">
						<li>
							<a
								className={(this.props.url === '/home')
								? 'active'
								: null}
								onClick={() => this.props.to('/home')}>
								<i
									className="ico home"
									style={{
									backgroundImage: `url(${icons})`
								}}></i>
								Home</a>
						</li>
						{this.props.name && this.props.email ?
							<li>
								<a
									className={(this.props.url === '/questions')
										? 'active'
										: null}
									onClick={() => this.props.to('/questions')}>
									<i
										className="ico login"
										style={{
											backgroundImage: `url(${icons})`
										}}></i>
									Start Test</a>
							</li>
							:
							<li>
								<a
									className={"disabled"}>
									<i
										className="ico login"
										style={{
											backgroundImage: `url(${icons})`
										}}></i>
									Start Test</a>
							</li>
						}

						{this.props.finished ? 
							<li>
								<a
									className={(this.props.url === '/results')
									? 'active'
									: null}
									onClick={() => this.props.to('/results')}>
									<i
										className="ico results"
										style={{
										backgroundImage: `url(${icons})`
									}}></i>
									Results
								</a>
								<a
									className={(this.props.url === '/results/business')
									? 'active'
									: null}
									onClick={() => this.props.to('/results/business')}>
									<i
										className="ico results"
										style={{
										backgroundImage: `url(${icons})`
									}}></i>
									Business Results
								</a>
							</li>

							:

							<li>
								<a className="disabled">
									<i
										className="ico results"
										style={{
										backgroundImage: `url(${icons})`
									}}></i>
									Results
								</a>
							</li> 
						}
						
						<li className="pull-right">
							<a
								className={(this.props.url === '/admin')
								? 'active'
								: null}
								onClick={() => this.props.to('/admin')}>
								<i
									className="ico admin"
									style={{
									backgroundImage: `url(${icons})`
								}}></i>
								Admin
							</a>
						</li>
					</ul>
				</div>
			</div>
		);
	}
}

export default SubHeader;


