import DATA from './appData';
import _ from 'underscore';

export default {
	url: '/home',
	questions: _.shuffle(DATA.questions),
	answers: {},
	completed: 0,
	finished: false,
	time: 'a few seconds',
	begin: new Date(),
	end: '',
	page: 1,
	// Used for priming the state
	criteria: DATA.criteria,
	// Used for calculation, sorting, and rearranging results
	criterias: DATA.criteria,
	profiles: DATA.profiles,
	profile: DATA.profiles[3],
	score: 0,
	total: 0,
	percent: 0,
	name: "",
	email: "",
	apiSent: false
}


