import React, { Component } from 'react';

import appState from './appState';

import { If } from 'react-if';
import { Layout, Home, Questions, Results, Admin, ResultsBusiness } from './appComponents';
import { to, time, tick, answerQuestion, handleChangePage, handleChangeResult, calcResults, handleName, handleEmail, sendResults } from './appActions'


class App extends Component {

	state = appState;
	
	to = to.bind(this);
	time = time.bind(this);
	tick = tick.bind(this);
	answerQuestion = answerQuestion.bind(this);
	handleChangePage = handleChangePage.bind(this);
	handleChangeResult = handleChangeResult.bind(this);
	calcResults = calcResults.bind(this);
	handleName = handleName.bind(this);
	handleEmail = handleEmail.bind(this);
	sendResults = sendResults.bind(this);
	
	render() {
		return (
			<Layout
				to={this.to}
				url={this.state.url}
				name={this.state.name}
				email={this.state.email}
				finished={this.state.finished} >
				<If condition={ this.state.url === '/home' }>
					<Home
						
						to={this.to}
						name={this.state.name}
						email={this.state.email}
						handleName={this.handleName}
						handleEmail={this.handleEmail}
						finished={this.state.finished} />
				</If>
				<If condition={ this.state.url === '/questions' }>
					<Questions
						time={this.state.time}
						tick={this.tick}
						page={this.state.page}
						to={this.to}
						completed={this.state.completed}
						questions={this.state.questions}
						handleChangePage={this.handleChangePage}
						answerQuestion={this.answerQuestion}
						finished={this.state.finished}
						/>
				</If>
				<If condition={ this.state.url === '/results' }>
					<Results
						sendResults={this.sendResults}		
						title={this.state.title}
						image={this.state.image}
						criterias={this.state.criterias}
						profile={this.state.profile}
						finished={this.state.finished}
						/>
				</If>
				<If condition={ this.state.url === '/admin' }>
					<Admin
						handleChangeResult={this.handleChangeResult}
						to={this.to} />
				</If>
				<If condition={ this.state.url === '/results/business' }>
					<ResultsBusiness
						name={this.state.name}	
						percent={this.state.percent}	
						criterias={this.state.criterias}
						profile={this.state.profile}	
						to={this.to} />
				</If>

			</Layout >
		);
	}
	
	
}

export default App;


